.alert {
	background-color: rgba($color: #fbf5ff, $alpha: 0.6);
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	z-index: 9999;
	display: flex;
	flex: 1;
	justify-content: center;
	align-items: center;

	height: 0;
	opacity: 0;

	&.show {
		height: 100vh;
		opacity: 1;
	}

	&.hide {
		height: 0;
		opacity: 0;
	}

	.alert-content {
		background-color: #bc0863;
		color: #ffffff;
		width: calc(80vw - 50px);
		padding: 50px 25px;
		border-radius: 10px;
		position: relative;
		text-align: center;

		svg {
			padding: 10px;
			position: absolute;
			top: 0;
			right: 0;
		}
	}

	transition: opacity 0.2s linear;
}
