.header {
	background: #bc0863;
	background: linear-gradient(90deg, #bc0863 0%, #dc3489 35%, #f35ba8 100%);
	color: #ffffff;
	display: flex;
	flex-direction: column;
	justify-content: center;
	min-height: 100px;

	.main-header {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		width: 100%;

		h1 {
			font-size: 1.2rem;
		}

		.icon-container {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 60px;
			height: 60px;
		}
	}

	.sub-header {
		display: flex;
	}
}