#word-list-screen {
	height: 100vh;
	display: flex;
	flex-direction: column;

	.search-container {
		position: relative;
		width: 100%;

		input {
			box-sizing: border-box;
			display: block;
			width: calc(100% - (1.5rem + 20px));
			padding: 0.375rem 0.75rem;
			padding-right: 45px;
			font-size: 1rem;
			line-height: 1.5;
			color: #ffffff;
			background-color: rgba(255, 255, 255, 0.3);
			margin: 20px;
			background-clip: padding-box;
			border: 1px solid #d73a87;
			border-radius: 0.25rem;
			transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;

			&::-webkit-input-placeholder {
				/* Edge */
				color: #ebb0d0;
			}

			&:-ms-input-placeholder {
				/* Internet Explorer 10-11 */
				color: #ebb0d0;
			}

			&::placeholder {
				color: #ebb0d0;
			}
		}

		.clear {
			color: #ffffff;
		}

		svg {
			position: absolute;
			top: 30px;
			right: 40px;
			color: #ebb0d0;
		}
	}

	.body {
		display: flex;
		flex-direction: column;
		flex: 1;
		overflow: auto;
		padding: 25px;

		.filter {
			margin-bottom: 25px;
		}

		.word-card {
			background-color: #ffffff;
			box-shadow: 0 0 10px 0px #ccc;
			display: flex;
			justify-content: center;
			align-items: center;
			margin-bottom: 15px;
			border-radius: 10px;

			.content {
				flex: 1;
				color: #000000;
				text-decoration: none;
				padding: 25px;
				font-size: 1.2rem;
			}

			.actions {
				padding: 0 10px;
				display: flex;
				flex-direction: row;

				.action-button {
					color: #d73a87;
					font-size: 1.5rem;
					display: flex;
					justify-content: center;
					align-items: center;
					margin: 0 5px;
					width: 40px;
					height: 40px;

					.edit {
						font-size: 1.3rem;
					}
				}
			}
		}
	}
}
