#add-word-screen {
	height: 100vh;
	display: flex;
	flex-direction: column;

	.body {
		display: flex;
		flex-direction: column;
		flex: 1;
		overflow: auto;
		padding: 25px;

		.form-group {
			margin-bottom: 15px;

			&.separator {
				margin-top: 15px;
				border-bottom: 1px solid #ddd;
				color: #888;
			}

			label {
				display: block;
				margin-bottom: 5px;
			}

			button {
				border: 1px solid #ced4da;
				border-radius: 0.25rem;
				padding: 10px;
				margin-bottom: 15px;
				justify-content: center;
				align-items: center;
				text-decoration: none;
				text-align: center;
				font-weight: bold;
			}

			.form-control {
				box-sizing: border-box;
				display: block;
				width: 100%;
				padding: 0.375rem 0.75rem;
				font-size: 1rem;
				line-height: 1.5;
				color: #495057;
				background-color: #fff;
				background-clip: padding-box;
				border: 1px solid #ced4da;
				border-radius: 0.25rem;
				transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
			}
		}
	}

	.footer {
		padding: 25px;

		button {
			border: 1px solid #ced4da;
			border-radius: 0.25rem;
			padding: 10px;
			width: 100%;
			justify-content: center;
			align-items: center;
			text-decoration: none;
			text-align: center;
			font-weight: bold;
			background-color: #bc0863;
			color: #ffffff;
		}
	}
}
