.drawer-wrapper {
	position: fixed;
	right: 0;
	top: 0;
	width: 100%;
	height: 100%;
	overflow: hidden;
	pointer-events: none;
	z-index: 150;
}

.drawer-wrapper--visible {
	pointer-events: auto;
}

.drawer-content {
	background-color: #E6E6E6;
	position: relative;
	max-width: 400px;
	width: 80%;
	height: 100%;
	box-shadow: 0 2px 6px rgba(0, 0, 0, 0.5);
	-webkit-transform: translateX(-103%);
	transform: translateX(-103%);
	display: flex;
	flex-direction: column;
	will-change: transform;
	z-index: 160;
	pointer-events: auto;

	.drawer-header {
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		height: 100px;
		padding: 15px;
		background: #bc0863;
		background: linear-gradient(90deg, #bc0863 0%, #dc3489 35%, #f35ba8 100%);
		color: #ffffff;
		font-size: 1.5rem;

		img {
			width: 100px;
			height: 100px;
		}
	}

	.drawer-body {
		margin-top: 10px;
		padding: 20px;

		a {
			text-decoration: none;
			color: #000000;
		}

		.title {

			margin-bottom: 10px;
			font-weight: bold;
			color: #999999;
		}

		.drawer-menu-item {
			display: flex;
			align-items: center;
			padding: 15px 0;

			&:not(:last-of-type) {
				border-bottom: 1px solid #dedede;
			}

			svg {
				background-color: #bc0863;
				color: #F8F8F8;
				width: 20px;
				height: 20px;
				padding: 10px;
				margin-right: 20px;
				border-radius: 10px;
			}

		}

		footer {
			color: #666666;
			position: absolute;
			bottom: 20px;
			left: 20px;
		}
	}
}

.drawer-wrapper--visible .drawer-content {
	-webkit-transform: none;
	transform: none;
}

.drawer-wrapper--animatable .drawer-content {
	transition: all 130ms ease-in;
}

.drawer-wrapper--visible.drawer-wrapper--animatable .drawer-content {
	transition: all 330ms ease-out;
}

.drawer-wrapper:after {
	content: "";
	display: block;
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.4);
	opacity: 0;
	will-change: opacity;
	pointer-events: none;
	transition: opacity 0.3s cubic-bezier(0, 0, 0.3, 1);
}

.drawer-wrapper--visible.drawer-wrapper:after {
	opacity: 1;
	pointer-events: auto;
}

.drawer-button {
	display: flex;
}
