#word-screen {
	height: 100vh;
	display: flex;
	flex-direction: column;

	.body {
		display: flex;
		flex-direction: column;
		flex: 1;
		overflow: auto;
		padding: 25px;

		.content {
			background-color: #ffffff;
			box-shadow: 0 0 10px 0px #ccc;
			border-radius: 10px;
			padding: 20px;
			margin-bottom: 20px;

			h2 {
				margin-top: 0;
			}

			p {
				white-space: pre-wrap;
				text-align: justify;

				&.label {
					margin-top: 45px;
					border-bottom: 1px solid #ddd;
					color: #888;
				}
			}
		}

		.button {
			text-decoration: none;
			background-color: #ffffff;
			color: #000;
			box-shadow: 0 0 10px 0px #ccc;
			border-radius: 10px;
			padding: 20px;
			margin-bottom: 20px;
			display: flex;
			flex-direction: row;

			img {
				width: 40px;
			}

			div {
				flex: 1;
				display: flex;
				justify-content: center;
				align-items: center;
				font-weight: bold;
				color: #888;
			}
		}
	}
}
