#home-screen {
	.body {
		padding: 25px;

		a.card {
			text-decoration: none;
			color: #ffffff;
			display: flex;
			justify-content: center;
			align-items: center;
			margin-bottom: 25px;
			padding: 25px;
			border-radius: 10px;

			& > div {
				flex: 1;

				.title {
					font-weight: bold;
					margin-bottom: 5px;
				}

				.subtitle {
					font-size: 12px;
				}
			}

			&:nth-child(1) {
				background-color: #9549D0;
				background: linear-gradient(90deg, #9549D0 0%, #6488E8 35%, #30CAFE 100%);
			}

			&:nth-child(2) {
				background-color: #7C529C;
				background: linear-gradient(90deg, #7C529C 0%, #AF4BBB 35%, #E542DB 100%);
			}
		}
	}
}